* {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

hr {
  border: 0;
  border-bottom: 1px solid #ddd;
  margin: 10px 0;
}

img {
  max-width: 100%;
  height: auto;
}

.App {
  margin: 0 auto;
  max-width: 1100px;
  padding-left: 10px;
  padding-right: 10px;
  word-wrap: break-word;
}

.App-menu {
  margin: 15px 0 20px;
}


.Compass {
  margin: 0 auto;
  max-width: 400px;
  overflow: hidden;
  position: relative;
  text-align: center;
  width: 90%;
}

.Compass:before {
  content: "";
  display: block;
  padding-top: 100%;
}

.Compass-north {
  position: relative;
  top: -25px;
}

.Compass-background,
.Compass-cache,
.Compass-distance,
.Compass-front {
  bottom: 20px;
  left: 20px;
  right: 20px;
  top: 20px;
  position: absolute;
}

.Compass-background {
  border-radius: 100%;
  border: 3px solid black;
}

.Compass-cache {
  color: red;
}

.Compass-cache,
.Compass-front {
  font-size: 20px;
  font-weight: bold;
}

.Compass-distance {
  font-size: 20px;
  font-weight: bold;
}

.Compass-distance:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

